<template>
  <div 
    @click="sectionClick"
    :class="{'completed-back': trainingSection.completed}"
    class="training-section"
  >
    <div class="img-container">
      <img :src="trainingImage(trainingSection.image)" alt="image">
    </div>
    <div class="section-description">
      <div class="section-title">{{ trainingSection.title }}</div>
      <div class="description">{{ trainingSection.text }}</div>
    </div>
    <!--<div>{{ trainingSection.link }}</div>-->
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'

  export default {
    props: {
      trainingSection: {},
      trainingIndex: Number
    },
    computed:{
      ...mapGetters([
        'trainingState'
      ]),

      summaryCompleted(){
        let summary = 0
        for (let i = 0; i < this.trainingState.trainingData.length; i++) {
          if (this.trainingState.trainingData[i].body.completed) {
            summary += 1
          }
        }
        return summary
      }
    },

    methods: {
      ...mapActions([
        'setTrainingCompleted',
        'setTrainingRightAnswers'
      ]),

      trainingImage(trainingImage){
        return require(`@/assets/images/training/${trainingImage}`) 
      },

      sectionClick(){
        console.log(this.summaryCompleted);
        this.setTrainingCompleted(this.trainingIndex)
        if (this.summaryCompleted === 3) {
          this.setTrainingRightAnswers(3)
        }
        window.open(this.trainingSection.link, '_blank')
      }
    },
  }
</script>

<style lang="scss">
  .completed-back{
    background-color: #E3FFD6 !important;
  }
</style>